<template>
    <div id="App" :class="{isOpenned : !openned}">
        <ul id="Menu" :style="styleApp">
            <li
                v-for="(item, index) in elements"
                :key="index"
                :class="{selected : selecionado === index}"
                :style="[selecionado === index ? styleItemSelected : styleItem]"
            >
                <div class="boxImg">
                    <img :src="item.img" alt="">
                </div>
                <div class="boxText">
                    <p class="text1" :style="styleText1">{{item.text}}</p> 
                    <p class="text2" :style="styleText2">{{item.text2}}</p> 
                </div>
            
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: "App",
    data: function(){
        return({
            elements: [
                {img: "./img/id.png", text: "Identidade", text2: "veja sua identidade"},
                {img: "./img/logo.png", text: "Serviço Emergencial", text2: "chamar por um serviço emergencial"},
                {img: "./img/id.png", text: "Banco", text2: "visualizar carteira bancária"},
                {img: "./img/logo2.png", text: "Saúde", text2: "visualizar status de saúde"},
                {img: "./img/logo3.png", text: "Posição Respawn", text2: "definir posição de respawn"},
                {img: "./img/id.png", text: "Banco", text2: "visualizar carteira bancária"},
                {img: "./img/logo2.png", text: "Saúde", text2: "visualizar status de saúde"},
                {img: "./img/logo3.png", text: "Posição Respawn", text2: "definir posição de respawn"},
                {img: "./img/id.png", text: "Banco", text2: "visualizar carteira bancária"},
                {img: "./img/logo2.png", text: "Saúde", text2: "visualizar status de saúde"},
                {img: "./img/logo3.png", text: "Posição Respawn", text2: "definir posição de respawn"}
            ],

            config:{
                positionX           : "10%",
                positionY           : "50%",
                size                : "1.0",
                maxHeight           : "80vh",

                itemColor           : "rgb(48, 48, 48)",
                itemSelectedColor   : "rgb(92, 1, 1)",

                text1Color          : "whitesmoke",
                text2Color          : "rgb(206, 206, 206)"
            },

            styleApp:{
                left                : "10%",
                top                 : "50%",
                transform           : "scale(1.0) translate( -10%, -50%)",
                maxHeight           : "80vh"
            },

            styleItem:{
                backgroundColor     : "rgb(48, 48, 48)"
            },

            styleItemSelected:{
                backgroundColor     : "rgb(92, 1, 1)"
            },

            styleText1:{
                color: "whitesmoke"
            },

            styleText2:{
                color: "rgb(206, 206, 206)"
            },

            selecionado             : 0,

            NameResource            : "icon_menu",

            openned                 : false,
        })
    },
    methods: {
        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.elements){
				this.selecionado = 0;
                this.elements = lua.elements;

                if(lua.configs){                    
                    this.styleApp.left      = lua.configs.positionX ? lua.configs.positionX : this.configs.positionX;
                    this.styleApp.top       = lua.configs.positionY ? lua.configs.positionY : this.configs.positionY;
                    this.styleApp.maxHeight = lua.configs.maxHeight ? lua.configs.maxHeight : this.configs.maxHeight;
                    const size              = lua.configs.size      ? lua.configs.size      : this.configs.size;
                    this.styleApp.transform = `scale(${size}) translate(-${this.styleApp.left}, -${this.styleApp.top})`;

                    this.styleItem.backgroundColor          = lua.configs.itemColor         ? lua.configs.itemColor         : this.configs.itemColor;
                    this.styleItemSelected.backgroundColor  = lua.configs.itemSelectedColor ? lua.configs.itemSelectedColor : this.configs.itemSelectedColor;

                    this.styleText1.color = lua.configs.text1Color ? lua.configs.text1Color : this.configs.text1Color;
                    this.styleText2.color = lua.configs.text2Color ? lua.configs.text2Color : this.configs.text2Color;
                }
                else{
                    
                    this.styleApp.left      = this.configs.positionX;
                    this.styleApp.top       = this.configs.positionY;
                    this.styleApp.maxHeight = this.configs.maxHeight;
                    const size              = this.configs.size;
                    this.styleApp.transform = `scale(${size}) translate(-${this.styleApp.left}, -${this.styleApp.top})`;
                    
                    this.styleItem.backgroundColor          = this.configs.itemColor;
                    this.styleItemSelected.backgroundColor  = this.configs.itemSelectedColor;

                    this.styleText1.color = this.configs.text1Color;
                    this.styleText2.color = this.configs.text2Color;
                }

                this.openned = true;
            }
            else if(lua.config_default){
                this.configs = lua.config_default;
            }
            else if(lua.upSelected){
                this.selecionado = this.selecionado === 0 ? this.elements.length-1 : this.selecionado - 1;
                const el = this.$el.querySelector('.selected');
                el.scrollIntoView({block: "center", behavior: "smooth"});
            }

            else if(lua.downSelected){
                this.selecionado = this.selecionado === this.elements.length-1 ? 0 : this.selecionado + 1;
                const el = this.$el.querySelector('.selected');
                el.scrollIntoView({block: "center", behavior: "smooth"});
            }

            else if(lua.enterSelected){
                fetch(`https://${this.NameResource}/enterSelected`, {
                    method: 'POST',
                    body: JSON.stringify({
                        selected: this.selecionado
                    })
                });  
            }

            else if(lua.goBack){
                this.openned = false;
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receiveLua);    
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
    }
}
</script>

<style>
    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        list-style: none;
        font-family: 'Roboto', sans-serif;
    }
    /* body{
        background-color: whitesmoke;
    } */

    #App{
        opacity: 0.95;
    }
    .isOpenned{
        display: none !important;
    }
    #Menu{
        position: absolute;
        width: 50vh;
        /* max-height: 80vh; */

        /* background: gray; */

        overflow: auto;
    }

    #Menu::-webkit-scrollbar-track {
        background-color: #ffffff00;
    }
    #Menu::-webkit-scrollbar {
        width: 0.3vh;
        background: rgba(214, 57, 57, 0);
    }
    #Menu::-webkit-scrollbar-thumb {
        background: rgba(214, 57, 57, 0);
        border-radius: 20px;
    }

    #Menu li{
        margin-bottom: 1vh;
        padding: 2vh;

        display: flex;
        flex-direction: row;

        max-height: 15vh;
        border-radius: 12px;

        transition: 0.3s all;
    }

    #Menu li:hover{
        background-color: rgb(92, 1, 1);
    }

    #Menu li .boxImg{
        width: 5vh;
        /* border-radius: 50%; */
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-content: center;
        align-items: center;
    }
    #Menu li .boxImg img{
        width: 100%;
    }
    #Menu li .boxText{
        flex: 1;
        margin-left: 2vh;
    }

    #Menu li .text1{
        /* color: whitesmoke; */
        font-size: 3vh;
    }

    #Menu li .text2{
        /* color: rgb(206, 206, 206); */
        font-size: 1.8vh;
    }
</style>